

#navbar{
    padding: 0px 0px 0px 0px;
    margin: 0px;
    font-size: 35px;
    text-align: center;
    text-shadow: 1px 1px 0px rgb(49, 47, 47);
}

#navbar-options {
    list-style-type: none;
    text-align: center;
}

#navbar-options li {
    display: inline-block;
    /* margin: 0px 100px 10px 100px; */
    margin: 0 auto;
    padding: 0px 100px 10px 100px;
    font-weight: bold;
}


#navbar-options li a{
    text-decoration: none;
    color: rgb(68, 67, 68);
    font-weight: bold;

}

#github-icon {
    width:200px;
    height:200px;
}

#linkedin-icon {
    width: 200px;
    height: 200px;
}

